import {NavLink} from "react-router-dom";
import userPhoto from "../../../images/no-avatar.png";
import usersCSS from "./users.module.css";
import React from "react";

const User = (props) => {
    return (
        <div key={props.id}>
            {props.id}
            <span>
                            <div key={"profileLink-" + props.id}>
                                <NavLink to={'/profile/' + props.id}>
                                <img alt={'Avatar'} src={props.photosSmall != null ? props.photosSmall : userPhoto}
                                     className={usersCSS.AvatarPhoto}/>
                                </NavLink>
                            </div>
                            <div key={"followButton-" + props.id}>
                                {
                                    props.followed ?
                                        <button disabled={props.followingInProgress.some(id => id === props.id)}
                                                onClick={() => {
                                                    props.unfollow(props.id);
                                                }}
                                        >
                                            UnFollow</button>
                                        : <button disabled={props.followingInProgress.some(id => id === props.id)}
                                                  onClick={() => {
                                                      props.follow(props.id);
                                                  }}
                                        >
                                            Follow</button>
                                }
                            </div>
                        </span>
            <span>
                            <span>
                                <div>
                                    {props.name}
                                </div>
                                <div>
                                    {props.status}
                                </div>
                            </span>
                            <span>
                                <div>
                                    {"user.Location.City"}, {"user.Location.Country"}
                                </div>
                                <div>

                                </div>
                            </span>
                        </span>
        </div>
    )
}

export default User;